h1 {
  color: green;
}

.note {
  display: flex;
  justify-content: space-between;
  padding: 2rem;
  color: whitesmoke;
  background-color: black;
  border-bottom: 1px solid lightgray;
}
.error {
  color: red;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}
